import { Fragment } from "react";
import AvatarIcon from "../../layouts/avatar/AvatarIcon";
import AvatarGroup from "@mui/material/AvatarGroup";
import {IUpComingAppointments} from '../../../types/upcoming.type';

const UpComingListContainer = (props : IUpComingAppointments) => {
    return (
        <div className="calendar-event-list-wrapper">
            <Fragment>
                <div className="calendar-event-list-col-1">
                    {props.date}
                </div>
                <div className="calendar-event-list-col-2">
                    {props.subject}
                </div>
                <div className="calendar-event-list-col-3">
                    <div className="users-stack">
                        <AvatarGroup total={0}>
                            {props.members.map((item) => (
                                <AvatarIcon key={item.id} alt="doctors" src="images" />
                            ))}
                        </AvatarGroup>
                    </div>
                </div>
                <div className="calendar-event-list-col-4">
                    <button className="btn-join">Join <span>{'>'}</span></button>
                </div>
            </Fragment>
    </div>
    )
}
export default UpComingListContainer;