import { Fragment, useState } from "react";

import { SnackbarAlert } from "../components/layouts/alert/Snackbar";
import DrawerLayout from "../components/DrawerLayout";
import RequestAppointment from "../components/RequestAppointment";
import Appointments from "../components/appointments/Appointments";

const Home = () => {

    const [open, setOpen] = useState<boolean>(false);
    const [alertMessage, setAlertMessage] = useState<string>("");
    const [isError, setIsError] = useState<boolean>(false);
    const [isRqstAppointment, setIsRqstAppointment] = useState<boolean>(false);

    const onCloseRqstAppointment = () => {
        setIsRqstAppointment(!isRqstAppointment);
    }


    return (
        <Fragment>
            <br />
            <div className="panel-container with-sidebar consumer-event-list">
                <Fragment>
                    <div className="calender-top-title-head main-head mb-5">
                        <div className="calender-top-title-head-left">
                            <h3>Appointments</h3>
                        </div>

                    </div>
                    <div className="calender-top-title-head mb-5">
                        <div className="calender-top-title-head-left">
                           <h2>Today</h2>
                        </div>
                        <div className="calender-top-title-head-right">
                            <span className="text-info text-btn">View History</span> &nbsp;&nbsp;
                            <button className="btn-default btn-md" onClick={onCloseRqstAppointment}>Request Appointment</button>
                        </div>
                    </div>


                </Fragment>

                <DrawerLayout
                    anchor="right"
                    width={400}
                    open={isRqstAppointment}
                    onClose={onCloseRqstAppointment}
                    component={<RequestAppointment onClose={onCloseRqstAppointment}/>}
                />
                <SnackbarAlert
                    open={open}
                    alertMessage={alertMessage}
                    isError={isError}
                    setOpen={setOpen}
                />

                <div className="mb-3">
                    <Appointments/>
                </div>
            </div>


        </Fragment>
    )
}
export default Home;