import { useState } from "react";
import { UserContextType } from "../../types/context.type";
import { IUserInfo } from "../../types/user.type";
import { useNavigate } from 'react-router-dom';
import { userContext } from "../state/user-context";


export const UserContextProvdier = (props: any) => {
    const navigate = useNavigate();
    const [isLoggedIn,setIsLoggedIn] = useState<boolean>(false);
    const [userInfo, setUserInfo] = useState<IUserInfo>({
            id: '',
            patientUserName: '',
            patientFirstName : '',
            patientLastName : '',
            patientPhoneNumber : '',
            patientEmail : '',
            hardCodedPatientID : ''
    });

    const logoutHandler = () => {
        localStorage.setItem("isLoggedIn", false.toString());
        localStorage.removeItem("userInfo");
        setIsLoggedIn(false);
        setUserInfo({
                id: '',
                patientUserName: '',
                patientFirstName : '',
                patientLastName : '',
                patientPhoneNumber : '',
                patientEmail : '',
                hardCodedPatientID : ''
        })
        navigate("../");
    }


    const updateUserInfoHandler = (userInfo: IUserInfo) => {
        localStorage.removeItem("userInfo");
        localStorage.setItem("userInfo", JSON.stringify(userInfo));
        setUserInfo(userInfo);
        setIsLoggedIn(true);
    }

    const onRefreshHandler = () => {
        if (userInfo == null) {
            const user = JSON.parse(localStorage.getItem('userInfo') || '{}');
            setUserInfo(user);
        }
    }
    const contextValue: UserContextType = {
        isLoggendIn: isLoggedIn,
        logout: logoutHandler,
        userInfo: userInfo,
        onRefresh: onRefreshHandler,
        updateUserInfo: updateUserInfoHandler
    }
    return <userContext.Provider value={contextValue}>{props.children}</userContext.Provider>
}