import { Fragment, useContext, useState } from "react"
import Input from "./form-items/Input"
import { useForm } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import { authContext } from "../context/state/auth-context";
import { SnackbarAlert } from "./layouts/alert/Snackbar";
import { ILoginInfo } from "../types/login.type";
import { IUserInfo } from "../types/user.type";
import { usePatientLoginLazyQuery } from "../graphql/hooks/login.generated";
import Loader from "./layouts/loader/Loader";

const Login = () => {

    const navigate = useNavigate();
    const authCtx = useContext(authContext);

    const { register, formState: { errors }, handleSubmit, getValues } = useForm();
    const [open, setOpen] = useState<boolean>(false);
    const [alertMessage, setAlertMessage] = useState<string>("");
    const [isError, setIsError] = useState<boolean>(false);

    const [onLogin, { data, error, loading }] = usePatientLoginLazyQuery();

    const onLoginHandler = async () => {
        const userData: ILoginInfo = {
            username: getValues('username'),
            password: getValues('password'),
            userType: "patient"
        }

        const res: any = await onLogin({
            variables: {
                username: userData.username,
                password: userData.password,
                userType: userData.userType
            }
        });
        if (res.data?.login) {
            const loginInfo = res.data?.login;
            const userDetails: IUserInfo = {
                    id: loginInfo[0]?.patient?.id,
                    patientUserName: loginInfo[0]?.patient?.patientUserName,
                    patientFirstName : loginInfo[0]?.patient?.patientFirstName,
                    patientLastName :  loginInfo[0]?.patient?.patientLastName,
                    patientPhoneNumber : loginInfo[0]?.patient?.patientPhoneNumber,    
                    patientEmail: loginInfo[0]?.patient?.patientEmail,
                    hardCodedPatientID : loginInfo[0]?.patient?.hardCodedPatientID
                }
            authCtx.login(userDetails);
            navigate('../home');
        }
        if (res?.error) {
            if (res.error?.message === "Exception while fetching data (/login) : User is attempting to login for the first time. Password reset is mandatory!") {
                navigate("../resetPassword", {
                    state: {
                        userName:userData.username
                    }
                });
            }
            else {
                setAlertMessage(res.error?.message);
                setIsError(true);
                setOpen(true);
            }
        }
        else {
            setAlertMessage("somethig went wrong..");
            setIsError(true);
            setOpen(true);
        }
    }

    return (
        <Fragment>
            {loading && <Loader />}
            <form onSubmit={handleSubmit(onLoginHandler)}>
                <div className="login-form-control">
                    <label>Username</label>
                    <Input register={register} errors={errors} fields={
                        {
                            controller: 'username',
                            isRequired: true,
                            minLength: 1,
                            disabled: false,
                            type: "text",
                            label: "Email",
                            placeholder: "Email address"
                        }}
                    />
                </div>
                <div className="login-form-control">
                    <label className="text-start">Password</label>
                    <Input register={register} errors={errors} fields={
                        {
                            controller: 'password',
                            isRequired: true,
                            minLength: 1,
                            disabled: false,
                            type: "password",
                            label: "Password",
                            placeholder: "Password"
                        }} />
                </div>
                <div className="login-form-btn">
                    <button type="submit">Login</button>
                </div>

            </form>
            <SnackbarAlert
                open={open}
                alertMessage={alertMessage}
                isError={isError}
                setOpen={setOpen}
            />
        </Fragment>
    )
}
export default Login