import { Fragment } from "react";
import TodayAppointments from "./today/TodayAppointments";
import moment from 'moment'
import UpCommingAppointments from "./upcoming/UpComingAppointments";
import {upCommingAppointments} from '../../dummyData/dummyData';

const Appointments = () => {
    return (
        <Fragment>
            <div className="mb-50">
                <p className="event-date">{moment().format("MMM D, YYYY")}</p>
                <TodayAppointments />
            </div>
            
            <div className="">
                <UpCommingAppointments data={upCommingAppointments}/>
            </div>
        </Fragment>
    )
}
export default Appointments;