
import {useState,Fragment,useContext,useEffect} from 'react';
import Images from '../../../constant/images';
import AvatarIcon from '../avatar/AvatarIcon';
import DrawerLayout from '../../DrawerLayout';
import UserProfile from '../../UserProfile';
import { userContext } from '../../../context/state/user-context';
import { useNavigate } from 'react-router-dom';

const Header = ()=>
{
    const [showUserProfile, setShowUserProfile] = useState<boolean>(false);
    const userCtx = useContext(userContext);

    return (
        <Fragment>
            <div className="nav-wrapper">
                <div className="nav">
                    <div className='nav-logo'>
                        <img src={Images.logoIcon} />
                    </div>
                    <div className='nav-profile'>
                        <div className='nav-account-name'>Consumer Wallet</div>
                        <div className='nav-profile-pic' onClick={()=>setShowUserProfile(!showUserProfile)}>
                            <AvatarIcon
                                alt={userCtx.userInfo.patientUserName}
                                height={35}
                                width={35}
                                src="home"
                            />
                        </div>
                    </div>
                </div>
            </div>
            <DrawerLayout
                anchor="right"
                width={400}
                open={showUserProfile}
                onClose={()=>setShowUserProfile(!showUserProfile)}
                component={<UserProfile/>}
            />
        </Fragment>
    )
}
export default Header;