import { Fragment } from "react";
import TodayListContainer from "./TodayListContainer";
import Loader from "../../layouts/loader/Loader";
import { useGetAllSessionsQuery } from "../../../graphql/hooks/getSessions.generated";

const TodayAppointments = () => {

    const { data, error, loading } = useGetAllSessionsQuery();

    return (
        <Fragment>
            {loading && <Loader />}
            <div className="">
                {data?.sessions?.map((item) => (
                    <TodayListContainer 
                        id={item?.id} 
                        startTime={item?.startTime}
                        taskDef={item?.taskDef} 
                        medProAsSessionOrganizer={item?.medProAsSessionOrganizer}
                        medProsAsGuests = {item?.medProsAsGuests}
                        patient = {item?.patient}
                        />
                ))}

            </div>
        </Fragment>
    )
}
export default TodayAppointments;