
import { Fragment } from "react";
import { ISelectProps } from "../../types/common.type";

const Select = (props : ISelectProps)=>
{
    const onChangHander = (e : any)=>
    {
        if(e.target.value !== "0")
        {
            props.setPhysicianId(e.target.value);
        }
       
    }
    return (
        <Fragment>
            <select className="form-control"  onChange={(e)=>onChangHander(e)}>
                <option disabled key={0} value={"0"}>Select Patient</option>
                {
                    props.items?.map((item : any,index : number)=>(
                        <option key={item.id} value={item?.id} >{item.userName}</option>
                    ))
                }
               
            </select>
        </Fragment>
    )
}
export default Select;