import { Fragment, useState } from 'react'
import { useForm } from 'react-hook-form';
import Select from './form-items/Select';
import { useGetAllPhysicainsQuery } from '../graphql/hooks/getAllPhysician.generated';
import Loader from './layouts/loader/Loader';

interface IRequestProps{
    onClose : any
}

const RequestAppointment = (props : IRequestProps) => {

    //const { register, formState: { errors }, handleSubmit, getValues } = useForm();
    const [sendInvitation, setSendInvitation] = useState<boolean>(false);
    const [physicianId,setPhysicianId] = useState<String>("");
    const [reason,setReason] = useState<string>("");

    const {data,error,loading} = useGetAllPhysicainsQuery();

    const onSubmitHandler = async(event: any) => {
        event?.preventDefault();
        props.onClose();
    }

    return (
        <Fragment>
            {loading && <Loader/>}
            <div className='right-sidebar-wrapper common-right-sidebar'>
                <div className="profile-info mt-4">
                    <div className="info-heading mt-0">Request Appointment</div>
                </div>
                {
                    sendInvitation ?
                        <>
                            <p>Invitation has been sent. </p>
                            {/* <button className='btn-default btn-md'>Start Now</button> */}
                        </>
                        :
                        <>
                         <form onSubmit={onSubmitHandler}>
                            <div className="form-field type-2 type-3">
                                <label className="text-start">Physician</label>
                                <Select 
                                    items={data?.medPro != undefined ? data?.medPro : []}
                                    setPhysicianId={setPhysicianId}
                                />
                            </div>
                            <div className="form-field type-2 type-3">
                                <label className="text-start">Reason</label>
                                <textarea onChange={(e)=>setReason(e.target.value)} className="form-control" style={{ height: "130px" }}></textarea>
                            </div>
                            
                            <br />
                            <div className="text-right"><button className="btn-default btn-md" type="submit">Send Request</button></div>
                        </form>
                        </>
                       
                }
            </div>

        </Fragment>
    )
}
export default RequestAppointment;