import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import { BrowserRouter } from "react-router-dom";
import { UserContextProvdier } from './context/provider/UserContextProvider';
import { AuthContextProvdier } from './context/provider/AuthContextProvider';
import { ApolloClient, InMemoryCache, ApolloProvider, gql } from '@apollo/client';

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);

const client = new ApolloClient({
 // uri: 'http://localhost:8081/graphql',
  uri : `http://${window.location.hostname}:8081/graphql`,
  cache: new InMemoryCache(),
  headers : {origin : "https://nestserver.z13.web.core.windows.net/"}
});

root.render(
<BrowserRouter basename='/consumer'>
<ApolloProvider client={client}>
    <UserContextProvdier>
      <AuthContextProvdier>
        <React.StrictMode>
          <App />
        </React.StrictMode>
      </AuthContextProvdier>
    </UserContextProvdier>
  </ApolloProvider>
  </BrowserRouter>
);

