import * as Types from '../types';

import { gql } from '@apollo/client';
import * as Apollo from '@apollo/client';
const defaultOptions = {} as const;
export type PatientLoginQueryVariables = Types.Exact<{
  username: Types.Scalars['String'];
  password: Types.Scalars['String'];
  userType: Types.Scalars['String'];
}>;


export type PatientLoginQuery = { __typename?: 'Query', login?: Array<{ __typename?: 'Session', id: string, patient?: { __typename?: 'Patient', id: string, patientUserName?: string | null, patientEmail?: string | null, patientFirstName?: string | null, patientLastName?: string | null, patientPhoneNumber?: any | null, hardCodedPatientID?: string | null } | null } | null> | null };


export const PatientLoginDocument = gql`
    query patientLogin($username: String!, $password: String!, $userType: String!) {
  login(username: $username, password: $password, userType: $userType) {
    id
    patient {
      id
      patientUserName
      patientEmail
      patientFirstName
      patientLastName
      patientPhoneNumber
      hardCodedPatientID
    }
  }
}
    `;

/**
 * __usePatientLoginQuery__
 *
 * To run a query within a React component, call `usePatientLoginQuery` and pass it any options that fit your needs.
 * When your component renders, `usePatientLoginQuery` returns an object from Apollo Client that contains loading, error, and data properties
 * you can use to render your UI.
 *
 * @param baseOptions options that will be passed into the query, supported options are listed on: https://www.apollographql.com/docs/react/api/react-hooks/#options;
 *
 * @example
 * const { data, loading, error } = usePatientLoginQuery({
 *   variables: {
 *      username: // value for 'username'
 *      password: // value for 'password'
 *      userType: // value for 'userType'
 *   },
 * });
 */
export function usePatientLoginQuery(baseOptions: Apollo.QueryHookOptions<PatientLoginQuery, PatientLoginQueryVariables>) {
        const options = {...defaultOptions, ...baseOptions}
        return Apollo.useQuery<PatientLoginQuery, PatientLoginQueryVariables>(PatientLoginDocument, options);
      }
export function usePatientLoginLazyQuery(baseOptions?: Apollo.LazyQueryHookOptions<PatientLoginQuery, PatientLoginQueryVariables>) {
          const options = {...defaultOptions, ...baseOptions}
          return Apollo.useLazyQuery<PatientLoginQuery, PatientLoginQueryVariables>(PatientLoginDocument, options);
        }
export type PatientLoginQueryHookResult = ReturnType<typeof usePatientLoginQuery>;
export type PatientLoginLazyQueryHookResult = ReturnType<typeof usePatientLoginLazyQuery>;
export type PatientLoginQueryResult = Apollo.QueryResult<PatientLoginQuery, PatientLoginQueryVariables>;