import { Fragment, useEffect } from "react";
import AvatarIcon from "../../layouts/avatar/AvatarIcon";
import AvatarGroup from "@mui/material/AvatarGroup";
import Loader from "../../layouts/loader/Loader";

interface ITodayListContainerProps
{
    id : string | undefined,
    taskDef : string | null |undefined,
    medProAsSessionOrganizer : any,
    medProsAsGuests : any,
    patient : any,
    startTime : any
}

const TodayListContainer = (props : ITodayListContainerProps) => {
    
    const onJoinSession = (sessionId : string | undefined)=>
    {
        if(sessionId != undefined)
        {
            if(props?.patient.hardCodedPatientID)
            {
                const id = parseInt(sessionId);
                window.open(`http://${window.location.hostname}/realtimedata/?userId=${props?.patient.hardCodedPatientID}&patient=${props?.patient.id}&sessionId=${id}`);
            }
           else
           {
                alert("something went wrong with HardCode Id for this patient");
           }
        }
        else
        {
            alert("something went wrong with SessionId")
        }

    }

    return (
        
        <div className="calendar-event-list-wrapper">
            {/* {loading && <Loader/>} */}
            <Fragment>
                <div className="calendar-event-list-col-1">
                    {props.startTime}
                </div>
                <div className="calendar-event-list-col-2">
                   {props.taskDef}
                </div>
                <div className="calendar-event-list-col-3">
                    <div className="users-stack">
                        <AvatarGroup total={0}>
                            {[1, 2, 3, 4].map((item) => (
                                <AvatarIcon alt="doctors" src="images" />
                            ))}
                        </AvatarGroup>
                    </div>
                </div>
                <div className="calendar-event-list-col-4">
                    <button className="btn-join" onClick={()=>onJoinSession(props.id)}>Join <span>{'>'}</span></button>
                </div>
            </Fragment>
        </div>
    )
}
export default TodayListContainer;