import { Fragment } from "react";
import moment from 'moment';
import UpCommingListContainer from "./UpComingListContainer";
import { IUpComingAppointmentsProps } from "../../../types/upcoming.type";

const UpComingAppointments = (props: IUpComingAppointmentsProps) => {
    return (
        <Fragment>
            <div className="calender-top-title-head mb-5">
                <div className="calender-top-title-head-left">
                    <h2>UPCOMING</h2>
                </div>
                <div className="calender-top-title-head-right">
                    
                </div>
            </div>
            {
                props.data.map((item) => (
                    <>
                        <p>{moment().format("MMM D, YYYY")}</p>
                        <div className="">
                            <UpCommingListContainer 
                                id={item.id}
                                date={item.date} 
                                day={item.day}
                                subject={item.subject}
                                members={item.members}
                                />
                        </div>
                    </>
                ))
            }

        </Fragment>
    )
}
export default UpComingAppointments;